import Swal from 'sweetalert2'
import { sweetAlertCommonParams } from '../../functions/utils'

export const referenceModal = async (oldData, node) => {
  const commonParams = sweetAlertCommonParams(node)
  
  return await Swal.fire({
    ...commonParams,
    title: 'Configure Reference',
    html:
      '<div class="form-group text-left mt-4">' +
        '<label for="reference-reference-postback">Title</label>' +
        '<input type="text" id="reference-reference-postback" class="form-control">' +
      '</div>'+
      '<div class="form-group text-left mt-2">' +
        '<label for="reference-choose-label">Choose label(s)</label>' +
        '<select id="reference-choose-label" class="form-control select2" multiple="">' +
        '</select>' +
      '</div>' +

      '<div class="form-group text-left mt-2">' +
        '<label for="reference-choose-sequence">Choose sequence</label>' +
        '<select id="reference-choose-sequence" class="form-control select2">' +
        '</select>' +
      '</div>'
      ,

    willOpen: () => {
      $('.select2').select2({ 
        width: '100%',
        dropdownParent: $('#swal2-content')
      })
      
      var page_table_id = window.xitFlowBuilderData.page_table_id
      var base_url = window.xitFlowBuilderData.base_url

      $.ajax({
        type: 'POST',
        url: base_url+'visual_flow_builder/get_label_sequence_dropdown',
        dataType: 'JSON',
        data: { page_table_id, requested_from: 'reference' },
        success:function(response){
          $("#reference-choose-label").html(response.label_dropdown);
          $("#reference-choose-sequence").html(response.drip_dropdown);

          $('#reference-choose-label').val(oldData.labelIds || '').trigger('change')
          $('#reference-choose-sequence').val(oldData.sequenceIdValue || '').trigger('change')
        }
      })

      // Sets old data if there is any
      $('#reference-reference-postback').val(oldData.title || '')
      $('#reference-choose-label').val(oldData.labelIds || '').trigger('change')
      $('#reference-choose-sequence').val(oldData.sequenceIdValue || '').trigger('change')
    },

    preConfirm: () => {
      const title = $('#reference-reference-postback').val()

      const labelIds = $('#reference-choose-label').val()
      const labelIdTexts = $('#reference-choose-label option:selected')
      
      const sequenceIdValue = $('#reference-choose-sequence').val()
      const sequenceIdText = $('#reference-choose-sequence option:selected').text()
      
      if (! title) {
        Swal.showValidationMessage('Please write a title.')
      }

      const labelIdTextsArray = []
      if (Array.isArray(labelIds) && labelIds.length > 0) {
        if (Object.keys(labelIdTexts).length > 0) {
          for (const text of labelIdTexts) {
            labelIdTextsArray.push(text.label)
          }
        }
      }

      return {
        title, 
        labelIds,
        labelIdTextsArray, 
        sequenceIdValue,
        sequenceIdText,
      }
    },
  })
}