<template>
  <div 
    class="dockmenu-content-elements"
    :style="{ height: elementContentHeight + 'px' }"
  >
    <div 
      id="content-elements-scrollbar"
      class="content-elements-scrollbar"
    >
      <div class="content-elements-elements">
        <type
          :key="type.name"
          :icon="type.icon"
          :title="type.name"
          v-for="type in types"
          v-show="type.visibility === 1"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Type from './Type'
import ComponentName from '../../const/component'
var sequence_addon = window.xitFlowBuilderData.sequence_addon
var user_input_flow_addon = window.xitFlowBuilderData.user_input_flow_addon

export default {
  name: 'Elements',
  components: {
    Type,
  },
  data: () => ({
    elementContentHeight: null,
    types: [
      {
        icon: 'fas fa-keyboard',
        name: ComponentName.TEXT,
        visibility: 1,
      },
      {
        icon: 'fas fa-file-image',
        name: ComponentName.IMAGE,
        visibility: 1,
      },
      {
        icon: 'fas fa-adjust',
        name: ComponentName.GENERIC_TEMPLATE,
        visibility: 1,
      }, 
      {
        icon: 'fas fa-images',
        name: ComponentName.CAROUSEL,
        visibility: 1,
      },
      {
        icon: 'fas fa-image',
        name: ComponentName.CAROUSEL_ITEM,
        visibility: 1,
      },
      {
        icon: 'fas fa-file-audio',
        name: ComponentName.AUDIO,
        visibility: 1,
      },       
       
      {
        icon: 'fas fa-file-video',
        name: ComponentName.VIDEO,
        visibility: 1,
      },      
             
      {
        icon: 'fas fa-file-download',
        name: ComponentName.FILE,
        visibility: 1,
      },
      {
        icon: 'fab fa-facebook-square',
        name: ComponentName.FACEBOOK_MEDIA,
        visibility: 1,
      },
      {
        icon: 'fas fa-cart-plus',
        name: ComponentName.ECOMMERCE,
        visibility: 1,
      },
      {
        icon: 'fas fa-stream',
        name: ComponentName.USER_INPUT_FLOW,
        visibility: user_input_flow_addon,
      },
      {
        icon: 'fas fa-bell',
        name: ComponentName.OTN,
        visibility: 1,
      }, 
      {
        icon: 'fas fa-paper-plane',
        name: ComponentName.BUTTON,
        visibility: 1,
      },
      {
        icon: 'fas fa-comment-dots',
        name: ComponentName.QUICK_REPLY,
        visibility: 1,
      },
      {
        icon: 'fas fa-map-signs',
        name: ComponentName.CONDITION,
        visibility: 1,
      },       
      {
        icon: 'fas fa-sort-numeric-down',
        name: ComponentName.SEQUENCE,
        visibility: sequence_addon,
      }, 
      {
        icon: 'fas fa-angle-double-right',
        name: ComponentName.SEQUENCE_SINGLE,
        visibility: sequence_addon,
      },
      {
        icon: 'far fa-hand-pointer',
        name: ComponentName.ACTION_BUTTON,
        visibility: 1,
      },
      {
        icon: 'fas fa-plane-departure',
        name: ComponentName.TRIGGER,
        visibility: 1,
      },
      {
        icon: 'fas fa-envelope',
        name: ComponentName.POSTBACK_NEW,
        visibility: 1,
      }, 
    ]
  }),

  methods: {
    handleScrollbar() {
      $('#content-elements-scrollbar').niceScroll({
        cursorcolor: '#6e7fff'
      })
    },

    handleElementContentHeight () {
      const offsetHeight = document.getElementById('xit-flow-builder').offsetHeight
      if (parseInt(offsetHeight, 10) <= 40) {
        this.elementContentHeight = 0
      } else {
        this.elementContentHeight = document.getElementById('xit-flow-builder').offsetHeight - 100
      }
    },    
  },

  mounted() {
    this.handleScrollbar()
    this.handleElementContentHeight()

    this.$nextTick(function() {
      window.addEventListener('resize', this.handleScrollbar)
      window.addEventListener('resize', this.handleElementContentHeight)
    })    
  },

  beforeDestroy() { // eslint-disable-line
    const contentElementsScrollbarElement = $('#content-elements-scrollbar')
    if (contentElementsScrollbarElement) {
      contentElementsScrollbarElement.getNiceScroll().remove()
    }
    
    window.removeEventListener('resize', this.handleScrollbar)
    window.removeEventListener('resize', this.handleElementContentHeight)   
  }  
}
</script>

<style scoped>
/* Important for nicescroll */
.content-elements-scrollbar {
  height: 100% !important;
}
.content-elements-elements {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 10px 6px;
}
</style>
