import Swal from 'sweetalert2'

import {
  hasDuplicates,
  uniqueId as getUniqueId, 
  sweetAlertCommonParams,
  getSelectOptionsFromArrayOfObjects,
} from '../../functions/utils'

import { 
  conditionSystemFieldVariables,
  conditionSystemFieldOperators 
} from '../../data'

// eslint-disable-next-line
export const conditionModal = async (oldData, node) => {

  const commonParams = sweetAlertCommonParams(node, '25rem')  

  return await Swal.fire({
    ...commonParams,
    title: 'Configure Condition',
    html: getHtmlForModal(oldData),

    willOpen: () => {
      $('.select2').select2({ 
        width: '100%',
        dropdownParent: $('#swal2-content')
      })
      
      getLabelViaAjax(function(response) {
        const html = getSelectOptions(response)
        window.xitFlowBuilderConditionLabels = html

        if (oldData.system_field_variable) {
          oldData.system_field_variable.forEach((val, idx) => {
            if ('contact_group_id' === val) {
              const element = '#condition-system-field-contact-group-id-group-' + idx + ' .system-field-contact-group-id'
              $(element).html(html)
              $(element).val(oldData.system_field_contact_group_id_selected_values).trigger('change')
            }
          })
        }
      })

      getCustomFieldVariableViaAjax(function(response) {
        const html = response.custom_field_variable_dropdown
        window.xitFlowBuilderCustomFieldVariableDropdown = html

        if (oldData.custom_field_variable) {
          oldData.custom_field_variable.forEach((val, idx) => {
            if (0 === idx) {
              window.xitFlowBuilderCustomFieldVariableDefaultValue = val
            }
  
            const element = '#condition-custom-field-variable-group-' + idx + ' .custom-field-variable'
            $(element).html(html)
            $(element).val(val).trigger('change')
          })
        } else {
          const element = '#condition-custom-field-variable-group-0 .custom-field-variable'
          $(element).html(html)
        }
      })

      toggleFormTagForSystemFieldVariable()
      toggleFormTagForSystemFieldOperator()
      setRestrictedOperatorIfSystemFieldVariable()
      setRestrictedOperatorIfCustomFieldVariable()

      $('#system-field-row-add').on('click', function() {
        const uniqueId = getUniqueId()
        const systemFieldHtml = getSystemFieldHtml(uniqueId)
        $('#system-field-wrapper').append(systemFieldHtml)

        $('.select2').select2({ 
          width: '100%',
          dropdownParent: $('#swal2-content')
        })

        return false
      })

      $('#custom-field-row-add').on('click', function() {
        const uniqueId = getUniqueId()
        const customFieldHtml = getCustomFieldHtml(uniqueId)
        $('.custom-field-wrapper').append(customFieldHtml)
        
        const element = $('#condition-custom-field-variable-group-' + uniqueId + ' .custom-field-variable')
        element.html(window.xitFlowBuilderCustomFieldVariableDropdown)

        $('.select2').select2({ 
          width: '100%',
          dropdownParent: $('#swal2-content')
        })

        return false
      })

      $(document).on('click', '.field-row-remove', function() {
        $(this).parent().parent().remove()
      })
    },

    preConfirm: () => {
      if (! Swal.isLoading()) {
        Swal.showLoading()
      }

      const all_match = $('#condition-match-all').prop('checked')
      const any_match = $('#condition-match-anything').prop('checked')

      const system_field_variable = $('.system-field-variable').map((index, element) => element.value).get() // eslint-disable-line
      const system_field_variable_selected_texts = $('.system-field-variable option:selected').map((index, element) => element.label).get() // eslint-disable-line

      const system_field_operator = $('.system-field-operator').map((index, element) => element.value).get() // eslint-disable-line
      const system_field_operator_selected_texts = $('.system-field-operator option:selected').map((index, element) => element.label).get() // eslint-disable-line
      
      const system_field_variable_value = $('.system-field-variable-value').map((index, element) => element.value).get() // eslint-disable-line
      
      const system_field_gender = $('.system-field-gender').map((index, element) => element.value).get() // eslint-disable-line
      const system_field_gender_selected_texts = $('.system-field-gender option:selected').map((index, element) => element.label).get() // eslint-disable-line
      
      const system_field_contact_group_id = $('.system-field-contact-group-id').map((index, element) => element.value).get() // eslint-disable-line
      const system_field_contact_group_id_selected_values = $('.system-field-contact-group-id option:selected').map((index, element) => element.value).get() // eslint-disable-line
      const system_field_contact_group_id_selected_texts = $('.system-field-contact-group-id option:selected').map((index, element) => element.label).get() // eslint-disable-line
      
      const custom_field_variable = $('.custom-field-variable').map((index, element) => element.value).get() // eslint-disable-line
      const custom_field_variable_selected_values = $('.custom-field-variable option:selected').map((index, element) => element.value).get() // eslint-disable-line
      const custom_field_variable_selected_texts = $('.custom-field-variable option:selected').map((index, element) => element.label).get() // eslint-disable-line
      
      const custom_field_operator = $('.custom-field-operator').map((index, element) => element.value).get() // eslint-disable-line
      const custom_field_operator_selected_texts = $('.custom-field-operator option:selected').map((index, element) => element.label).get() // eslint-disable-line
      
      const custom_field_variable_value = $('.custom-field-variable-value').map((index, element) => element.value).get() // eslint-disable-line

      if (system_field_variable.filter(Boolean).length > 0) {
        if (hasDuplicates(system_field_variable)) {
          Swal.showValidationMessage('System field variable has duplicate values.')
          Swal.hideLoading()
  
          return false
        }

        system_field_variable.forEach((val, idx) => {
          if ('gender' === val) {
            if ('equal' !== system_field_operator[idx]) {
              Swal.showValidationMessage('#' + (idx + 1) + ' system field row operator must be set to equal.')
              Swal.hideLoading()
              
              return false
            }
    
            if (! system_field_gender[idx]) {
              Swal.showValidationMessage('#' + (idx + 1) + ' system field row gender type is empty.')
              Swal.hideLoading()

              return false
            }
          } else if ('contact_group_id' === val) {
            if (! system_field_contact_group_id[idx]) {
              Swal.showValidationMessage('#' + (idx + 1) + ' system field row contact group IDs are empty.')
              Swal.hideLoading()

              return false
            }
          } else {
            if ('has_value' !== system_field_operator[idx] && ! system_field_variable_value[idx]) {
              Swal.showValidationMessage('#' + (idx + 1) + ' system field row variable value is empty.')
              Swal.hideLoading()

              return false
            }
          }
        })
      }

      if (custom_field_variable_selected_values.filter(Boolean).length > 0) {
        if (hasDuplicates(custom_field_variable)) {
          Swal.showValidationMessage('Custom field variable has duplicate values.')
          Swal.hideLoading()

          return false
        }

        custom_field_variable.forEach((val, idx) => {
          if (! val) {
            Swal.showValidationMessage('#' + (idx + 1) + ' custom field row variable is empty.')
            Swal.hideLoading()

            return false
          }

          if (val && ! custom_field_operator[idx]) {
            Swal.showValidationMessage('#' + (idx + 1) + ' custom field row operator is empty.')
            Swal.hideLoading()

            return false
          }

          if (val && ('has_value' !== custom_field_operator[idx]) && ! custom_field_variable_value[idx]) {
            Swal.showValidationMessage('#' + (idx + 1) + ' custom field row variable value is empty.')
            Swal.hideLoading()

            return false
          }
        })
      }

      if (Swal.isLoading()) {
        Swal.hideLoading()
      }

      return {
        all_match,
        any_match,

        system_field_variable,
        system_field_variable_selected_texts,
        
        system_field_operator,
        system_field_operator_selected_texts,

        system_field_variable_value,

        system_field_gender,
        system_field_gender_selected_texts,

        system_field_contact_group_id,
        system_field_contact_group_id_selected_values,
        system_field_contact_group_id_selected_texts,

        custom_field_variable,
        custom_field_variable_selected_values,
        custom_field_variable_selected_texts,

        custom_field_operator,
        custom_field_operator_selected_texts,

        custom_field_variable_value,
      }
    },    
  })
}

const toggleFormTagForSystemFieldVariable = () => {
  $(document).on('change', '.system-field-variable', function() {
    const val = $(this).val()
    const ancestor = $(this).parent().parent().parent()

    if ('gender' === val) {
      $(ancestor).find('div[id^="condition-system-field-contact-group-id-group-"]').removeClass('d-block').addClass('d-none')
      $(ancestor).find('div[id^="condition-system-field-variable-value-group-"]').removeClass('d-block').addClass('d-none')
      $(ancestor).find('div[id^="condition-system-field-gender-group-"]').removeClass('d-none').addClass('d-block')
      $(ancestor).find('.system-field-operator').val('equal').trigger('change')
    } else if ('contact_group_id' === val) {
      $(ancestor).find('div[id^="condition-system-field-variable-value-group-"]').removeClass('d-block').addClass('d-none')
      $(ancestor).find('div[id^="condition-system-field-gender-group-"]').removeClass('d-block').addClass('d-none')
      $(ancestor).find('div[id^="condition-system-field-contact-group-id-group-"]').removeClass('d-none').addClass('d-block')
      
      // Populate select options
      const element = $(ancestor).find('.system-field-contact-group-id')
      $(element).html(window.xitFlowBuilderConditionLabels)
    } else {
      $(ancestor).find('div[id^="condition-system-field-contact-group-id-group-"]').removeClass('d-block').addClass('d-none')
      $(ancestor).find('div[id^="condition-system-field-gender-group-"]').removeClass('d-block').addClass('d-none')
      
      const operatorSelected = $(ancestor).find('.system-field-operator').val()
      if ('has_value' !== operatorSelected) {
        $(ancestor).find('div[id^="condition-system-field-variable-value-group-"]').removeClass('d-none').addClass('d-block')
      }
    }
  });

  return false
}

const toggleFormTagForSystemFieldOperator = () => {
  $(document).on('change', '.system-field-operator', function() {
    const val = $(this).val()
    const ancestor = $(this).parent().parent().parent()
    const variableSelected = $(ancestor).find('.system-field-variable').val()
    
    if ('gender' === variableSelected) {
      if ('equal' !== val) {
        $(this).val('equal').trigger('change')
      }
    } else if ('contact_group_id' === variableSelected) {
      if ('contains' !== val) {
        $(this).val('contains').trigger('change')
      }
    }

    const variableValues = ['gender', 'contact_group_id']

    if ('has_value' === val && ! (variableValues.indexOf(variableSelected) > -1)) {
      $(ancestor).find('div[id^="condition-system-field-variable-value-group-"]').removeClass('d-block').addClass('d-none')
    } else if ('has_value' !== val && ! (variableValues.indexOf(variableSelected) > -1)) {
      $(ancestor).find('div[id^="condition-system-field-variable-value-group-"]').removeClass('d-none')
    }
  })

  return false
}

const setRestrictedOperatorIfSystemFieldVariable = () => {
  $(document).on('change', '.system-field-variable', function() {
    const val = $(this).val()
    const ancestor = $(this).parent().parent().parent()
    const selectedOperator = $(ancestor).find('.system-field-operator').val()
    if ('gender' === val) {
      if ('equal' !== selectedOperator) {
        $(ancestor).find('.system-field-operator').val('equal').trigger('change')
      }
    } else if ('contact_group_id' === val) {
      if ('contains' !== selectedOperator) {
        $(ancestor).find('.system-field-operator').val('contains').trigger('change')
      }
    }
  })
}

const setRestrictedOperatorIfCustomFieldVariable = () => {
  $(document).on('change', '.custom-field-operator', function() {
    const val = $(this).val()
    const ancestor = $(this).parent().parent().parent()
    
    if ('has_value' === val) {
      $(ancestor).find('div[id^="condition-custom-field-variable-value-group-"]').removeClass('d-block').addClass('d-none')
    } else {
      $(ancestor).find('div[id^="condition-custom-field-variable-value-group-"]').removeClass('d-none').addClass('d-block')
    }
  });

  return false
}

