import Rete from 'rete'

import TextTemplate from './TextTemplate'
import SocketName from '../../const/socket'
import ComponentName from '../../const/component'

export default class TextComponent extends Rete.Component {
  constructor(config) {
    super(ComponentName.TEXT)

    this.data.component = TextTemplate
    this.socket = config ? config.sockets : null
  }

  builder(node) {
    this.setConnection()

    const textInput = new Rete.Input(SocketName.TEXT_INPUT, 'Reply', this.socket.textInput, false)
    const textOutput = new Rete.Output(SocketName.TEXT_OUTPUT, 'Next', this.socket.textOutput, false)
    const textOutputButton = new Rete.Output(SocketName.TEXT_OUTPUT_BUTTON, 'Buttons', this.socket.textOutputButton, true)
    const textOutputQuickreply = new Rete.Output(SocketName.TEXT_OUTPUT_QUICKREPLY, 'Quick Replies', this.socket.textOutputQuickreply, true)

    return node
      .addInput(textInput)
      .addOutput(textOutput)
      .addOutput(textOutputButton)
      .addOutput(textOutputQuickreply)
  }

  // eslint-disable-next-line
  worker(node, inputs, outputs) {
    outputs[SocketName.TEXT_OUTPUT] = node.data
    outputs[SocketName.TEXT_OUTPUT_BUTTON] = node.data
  }
  
  setConnection() {
    // Text may take connections from:
    this.socket.textInput.combineWith(this.socket.referenceOutput)
    this.socket.textInput.combineWith(this.socket.newPostbackOutput)
    this.socket.textInput.combineWith(this.socket.textOutput)
    this.socket.textInput.combineWith(this.socket.carouselOutput)
    this.socket.textInput.combineWith(this.socket.facebookMediaOutput)
    this.socket.textInput.combineWith(this.socket.imageOutput)
    this.socket.textInput.combineWith(this.socket.videoOutput)
    this.socket.textInput.combineWith(this.socket.audioOutput)
    this.socket.textInput.combineWith(this.socket.fileOutput)
    this.socket.textInput.combineWith(this.socket.ecommerceOutput)
    this.socket.textInput.combineWith(this.socket.genericTemplateOutput)
    this.socket.textInput.combineWith(this.socket.otnOutput)

    // Text may be connected to:
    this.socket.textOutput.combineWith(this.socket.textInput)
    this.socket.textOutput.combineWith(this.socket.carouselInput)
    this.socket.textOutput.combineWith(this.socket.facebookMediaInput)
    this.socket.textOutput.combineWith(this.socket.imageInput)
    this.socket.textOutput.combineWith(this.socket.videoInput)
    this.socket.textOutput.combineWith(this.socket.audioInput)
    this.socket.textOutput.combineWith(this.socket.fileInput)
    this.socket.textOutput.combineWith(this.socket.userInputFlowInput)
    this.socket.textOutput.combineWith(this.socket.genericTemplateInput)
    this.socket.textOutput.combineWith(this.socket.ecommerceInput)
    this.socket.textOutput.combineWith(this.socket.otnInput)

    // Output (Buttons): QuickReply, Buttons
    // textOutputButton.combineWith(this.socket.quickReplyInput)
    this.socket.textOutputButton.combineWith(this.socket.buttonInput)
    this.socket.textOutputQuickreply.combineWith(this.socket.quickReplyInput)    
  }
}