import Rete from 'rete'

import SocketName from '../../const/socket'
import ComponentName from '../../const/component'
import CarouselTemplate from './CarouselTemplate'

export default class CarouselComponent extends Rete.Component {
  constructor(config) {
    super(ComponentName.CAROUSEL)

    this.data.component = CarouselTemplate
    this.socket = config ? config.sockets : null
  }

  builder(node) {
    this.setConnection()

    const carouselInput = new Rete.Input(SocketName.CAROUSEL_INPUT, 'Reply', this.socket.carouselInput, false)
    const carouselOutput = new Rete.Output(SocketName.CAROUSEL_OUTPUT, 'Next', this.socket.carouselOutput, false)
    const carouselOutputItems = new Rete.Output(SocketName.CAROUSEL_OUTPUT_ITEMS, 'Items', this.socket.carouselOutputItems, true)
    const carouselOutputQuickreply = new Rete.Output(SocketName.CAROUSEL_OUTPUT_QUICKREPLY, 'Quick Replies', this.socket.carouselOutputQuickreply, true)

    return node
      .addInput(carouselInput)
      .addOutput(carouselOutput)
      .addOutput(carouselOutputItems)
      .addOutput(carouselOutputQuickreply)
  }

  // eslint-disable-next-line
  worker(node, inputs, outputs) {
    outputs[SocketName.CAROUSEL_OUTPUT] = node.data
    outputs[SocketName.CAROUSEL_OUTPUT_ITEMS] = node.data
  }
  
  setConnection() {
    // Carousel may take connections from:
    this.socket.carouselInput.combineWith(this.socket.referenceOutput)
    this.socket.carouselInput.combineWith(this.socket.newPostbackOutput)
    this.socket.carouselInput.combineWith(this.socket.textOutput)
    this.socket.carouselInput.combineWith(this.socket.carouselOutput)
    this.socket.carouselInput.combineWith(this.socket.facebookMediaOutput)
    this.socket.carouselInput.combineWith(this.socket.imageOutput)
    this.socket.carouselInput.combineWith(this.socket.videoOutput)
    this.socket.carouselInput.combineWith(this.socket.audioOutput)
    this.socket.carouselInput.combineWith(this.socket.fileOutput)
    this.socket.carouselInput.combineWith(this.socket.ecommerceOutput)
    this.socket.carouselInput.combineWith(this.socket.genericTemplateOutput)
    this.socket.carouselInput.combineWith(this.socket.otnOutput)

    // Carousel may be connected to:
    this.socket.carouselOutput.combineWith(this.socket.textInput)
    this.socket.carouselOutput.combineWith(this.socket.carouselInput)
    this.socket.carouselOutput.combineWith(this.socket.facebookMediaInput)
    this.socket.carouselOutput.combineWith(this.socket.imageInput)
    this.socket.carouselOutput.combineWith(this.socket.videoInput)
    this.socket.carouselOutput.combineWith(this.socket.audioInput)
    this.socket.carouselOutput.combineWith(this.socket.fileInput)
    this.socket.carouselOutput.combineWith(this.socket.ecommerceInput)
    this.socket.carouselOutput.combineWith(this.socket.userInputFlowInput)
    this.socket.carouselOutput.combineWith(this.socket.genericTemplateInput)
    this.socket.carouselOutput.combineWith(this.socket.otnInput)

    // Output (CarouselItem): CarouselItem
    this.socket.carouselOutputItems.combineWith(this.socket.carouselSingleInput)
    this.socket.carouselOutputQuickreply.combineWith(this.socket.quickReplyInput)    
  }
}