import Rete from 'rete'

import SocketName from '../../const/socket'
import ComponentName from '../../const/component'
import EcommerceTemplate from './EcommerceTemplate'

export default class EcommerceComponent extends Rete.Component {
  constructor (config) {
    super(ComponentName.ECOMMERCE)

    this.data.component = EcommerceTemplate
    this.socket = config ? config.sockets : null
  }

  builder (node) {
    this.setConnection()

    const ecommerceInput = new Rete.Input(SocketName.ECOMMERCE_INPUT, 'Reply', this.socket.ecommerceInput, false)
    const ecommerceOutput = new Rete.Output(SocketName.ECOMMERCE_OUTPUT, 'Next', this.socket.ecommerceOutput, false)
    const ecommerceOutputQuickreply = new Rete.Output(SocketName.ECOMMERCE_OUTPUT_QUICKREPLY, 'Quick Replies', this.socket.ecommerceOutputQuickreply, true)

    return node
      .addInput(ecommerceInput)
      .addOutput(ecommerceOutput)
      .addOutput(ecommerceOutputQuickreply)
  }

  // eslint-disable-next-line
  worker (node, inputs, outputs) {
    outputs[SocketName.ECOMMERCE_OUTPUT] = node.data
  }

  setConnection() {
    // Ecommerce may take connections from:
    this.socket.ecommerceInput.combineWith(this.socket.referenceOutput)
    this.socket.ecommerceInput.combineWith(this.socket.newPostbackOutput)
    this.socket.ecommerceInput.combineWith(this.socket.textOutput)
    this.socket.ecommerceInput.combineWith(this.socket.carouselOutput)
    this.socket.ecommerceInput.combineWith(this.socket.facebookMediaOutput)
    this.socket.ecommerceInput.combineWith(this.socket.imageOutput)
    this.socket.ecommerceInput.combineWith(this.socket.videoOutput)
    this.socket.ecommerceInput.combineWith(this.socket.audioOutput)
    this.socket.ecommerceInput.combineWith(this.socket.fileOutput)
    this.socket.ecommerceInput.combineWith(this.socket.ecommerceOutput)
    this.socket.ecommerceInput.combineWith(this.socket.genericTemplateOutput)
    this.socket.ecommerceInput.combineWith(this.socket.otnOutput)

    // Ecommerce may be connected to:
    this.socket.ecommerceOutput.combineWith(this.socket.textInput)
    this.socket.ecommerceOutput.combineWith(this.socket.carouselInput)
    this.socket.ecommerceOutput.combineWith(this.socket.facebookMediaInput)
    this.socket.ecommerceOutput.combineWith(this.socket.imageInput)
    this.socket.ecommerceOutput.combineWith(this.socket.videoInput)
    this.socket.ecommerceOutput.combineWith(this.socket.audioInput)
    this.socket.ecommerceOutput.combineWith(this.socket.fileInput)
    this.socket.ecommerceOutput.combineWith(this.socket.ecommerceInput)
    this.socket.ecommerceOutput.combineWith(this.socket.userInputFlowInput)
    this.socket.ecommerceOutput.combineWith(this.socket.genericTemplateInput)
    this.socket.ecommerceOutput.combineWith(this.socket.otnInput)

    // Output (QuickReply): QuickReply
    this.socket.ecommerceOutputQuickreply.combineWith(this.socket.quickReplyInput)    
  }
}