export const newPostbackHtml = (node) => {
  const isHidable = node.newPostbackModalHidableFieldsStatus || false
  const hidableClass = isHidable ? 'd-none' : ''
  const isSequenceAddonAvailable = !! window.xitFlowBuilderData.sequence_addon

  return isSequenceAddonAvailable ?
    '<div class="form-group text-left mt-4">' +
      '<label for="new-postback-postback-title">Title</label>' +
      '<input type="text" id="new-postback-postback-title" class="form-control">' +
    '</div>' +
    
    '<div class="form-group text-left mt-2 ' + hidableClass + '">' +
      '<label for="new-postback-choose-label">Choose label(s)</label>' +
      '<select id="new-postback-choose-label" class="form-control select2 new-postback-hidable-field" multiple="">' +
      '</select>' +
    '</div>' +
    
    '<div class="form-group text-left mt-2 ' + hidableClass + '">' +
      '<label for="new-postback-choose-sequence">Choose sequence</label>' +
      '<select id="new-postback-choose-sequence" class="form-control select2 new-postback-hidable-field">' +
      '</select>' +
    '</div>'
    
    :

    '<div class="form-group text-left mt-4">' +
      '<label for="new-postback-postback-title">Title</label>' +
      '<input type="text" id="new-postback-postback-title" class="form-control">' +
    '</div>' +
    
    '<div class="form-group text-left mt-2 ' + hidableClass + '">' +
      '<label for="new-postback-choose-label">Choose label(s)</label>' +
      '<select id="new-postback-choose-label" class="form-control select2 new-postback-hidable-field" multiple="">' +
      '</select>' +
    '</div>'
}